import React from 'react';
import {Form,Input,Button,Select,Modal} from 'antd';
const FormItem = Form.Item;
const Option = Select.Option;
 
class EditBox extends React.Component{//es6AddUser
     constructor(props){//
         super(props);
         this.state = {
             visible:false
         };
         this.handleAdd = this.handleAdd.bind(this);
         this.handleSubmit = this.handleSubmit.bind(this);
         this.handleOk = this.handleOk.bind(this)
         this.handleClear = this.handleClear.bind(this)
     }
    handleAdd() {
        this.setState({
            visible: true
        });
    }
    handleSubmit(e){//
        e.preventDefault();
         this.props.form.validateFieldsAndScroll((err,values)=>{
             if(!err){
                 //console.log('',values);
                 this.setState({
                     visible:false
                 })
                 this.props.form.resetFields();//
                 //appendPersonvalues
                 this.props.callback(values);
             }
         })
    }
 
    handleClear(){
        this.props.form.resetFields();
    }
 
    handleOk() {
        this.setState({
            visible: false
            });
    }
    render(){
 
     const {getFieldDecorator} = this.props.form;
     const formItemLayout = {
         labelCol:{span : 6},
         wrapperCol:{span: 14}
     };
     const tailFormItemLayout = {
         wrapperCol: {
             span: 14,
             offset: 8
         }
     };
        return(
            <div>
                <Button type="primary" onClick={this.handleAdd}></Button>
            <Modal title="" visible={this.state.visible} onCancel={this.handleOk} onOk={this.handleOk}>
                <Form onSubmit={this.handleSubmit}>
                    <FormItem {...formItemLayout} label = "boxid"  hasFeedback>
                        {getFieldDecorator('bid', {
                            rules:[{
                                required:true,message:'id'
                            }]
                        })(
                            <Input placeholder="id"/>
                            )}
                    </FormItem>
                    <FormItem {...formItemLayout} label = ""  hasFeedback>
                        {getFieldDecorator('pn', {
                            rules:[{
                                required:true,message:''
                            }]
                        })(
                            <Input placeholder=""/>
                            )}
                        </FormItem>
                        <FormItem {...formItemLayout} label="" hasFeedback>
                        {getFieldDecorator('price',{
                            rules:[{required:true,message:''}]
                        })(
                            <Input placeholder=""/>
                        )}
                    </FormItem>
                    
                    
                    <FormItem {...formItemLayout} label="" hasFeedback>
                        {getFieldDecorator('rest',{
                            rules:[{required:true,message:''}]
                        })(
                            <Input placeholder=""/>
                        )}
                    </FormItem>
                    <FormItem {...formItemLayout} label="" hasFeedback>
                        {getFieldDecorator('threshold',{
                            rules:[{required:true,message:''}]
                        })(
                            <Input placeholder=""/>
                        )}
                    </FormItem>
                    
                    <FormItem {...tailFormItemLayout} style={{padding:10}}>
                        <Button type="primary" htmlType="submit" size="large"></Button>
                        <Button type="primary" size="large" onClick={this.handleClear}></Button>
                    </FormItem>
                     </Form>
            </Modal>
            </div>
        );
    }
}

EditBox = Form.create()(EditBox); //getFieldDecorator;
 
export default EditBox;





